@Component({
    selector: 'app-rtf-app',
    templateUrl: './rtf-app.component.html',
    styleUrls: ['./rtf-app.component.css']
})
export class RtfAppComponent implements OnInit {

    @Input() bizObjectId: string;

    @Input()
    gridSelectMode: 'single' | 'multiple';
    /**
     * ç¡®è®¤äºä»¶
     */
    @Output()
    afterConfirm = new EventEmitter<RtfApp[]>();
    /**
     * å³é­äºä»¶
     */
    @Output()
    afterCancle = new EventEmitter<any>();
    // æ³¨å¥å°å¸®å©çç¡®è®¤ååæ¶æé®
    @ViewChild('btnOkAndCancle')
    btnOkAndCancleRef: TemplateRef<any>;

    data: RtfApp[];
    dataarray = [];
    gridData: any;

    /**
     * å·¦ä¾§æ åè¡¨æ°æ®
     */
    treeData: RtfApp[];
    /**
     * å½åéä¸­æ°æ®
     */
    curSelectedKeys: string[] = [];
    selectableSettings: SelectableSettings;
    selectedTreeNode: any;
    private currentLangCode: string;
    public title = '';

    i18nEntity: any = {};

    constructor(
        private rtfAppService: RtfAppService,
        @Optional() private i18nService: I18NresourceService,
        private farrisMsg: MessagerService,
    ) {
        if (!i18nService) {
            this.i18nService = new I18NresourceService(rtfAppService);
        }
        // this.i18nEntityInit();
    }

    ngOnInit() {
        this.selectableSettings = {
            checkboxOnly: true,
            // é»è®¤åé
            mode: !this.gridSelectMode ? 'single' : this.gridSelectMode,
            enabled: true
        };
        /*this.rtfAppService.getAppList().subscribe(
            res => {
                // å·¦ä¾§æ 
                this.treeData = res;
                // èåæç»æ°æ®
                const info = res.filter(item => item.layer === '4');
                info.forEach(element => {
                    this.dataarray = this.dataarray.concat(element.appInvoks);
                });
                // this.gridData = this.dataarray;
            },
            error => {
                console.log('è·åå¤±è´¥');
            }
        );*/
        // è·åææåºç¨æ°æ®
        this.rtfAppService.getAppListWithBizObjectData().subscribe(
            res => {
                this.treeData = res;
                const appDetails = res.filter((item: any) => item.layer === '4' || (item.layer === '5'));
                appDetails.forEach(ele => {
                    this.dataarray = this.dataarray.concat(ele.appInvoks);
                });
            },
            error => {
                this.farrisMsg.error("è·ååºç¨æ°æ®å¤±è´¥");
            }
        )
    }

    /**
     * æç´¢
     * @param filterValue æ¥è¯¢æ¡ä»¶
     */
    searchData(filterValue: string) {
        // ç©ºçæç´¢æ¯å¨é¨æ°æ®
        if (filterValue === null || filterValue === '') {
            this.gridData = this.data.concat();
            return;
        }
        const filter = filterValue.toLowerCase();
        // æç´¢å°ä¸çº§æ¶
        this.gridData = this.data.filter(item =>
            item.code.toLowerCase().includes(filter) ||
            item.name.toLowerCase().includes(filter));
    }

    /**
     * å·¦ä¾§æ åæ¢
     * @param arg å¯¹è±¡åæ°
     */
    leftAppTreeChanged(arg) {
        this.selectedTreeNode = arg;
        if (!('4' === arg.layer || '5' === arg.layer)) {
            this.gridData = [];
            return;
        }

        const selectedId = arg && arg.id;
        // æ ¹æ®ç¶å­å³ç³»è¿æ»¤å½åçé¢æ°æ®ï¼åå«ä¸ä¸çº§
        this.gridData = this.dataarray.filter(
            item => selectedId && item.appId === selectedId
        );
    }


    /**
     * è·åéä¸­æ°æ®
     */
    getSelectedObject() {
        const result = this.dataarray.filter(
            item => this.curSelectedKeys && this.curSelectedKeys.findIndex(select => select === item.id) >= 0
        );
        return result;
    }

    /**
     * ç¡®è®¤
     */
    confirm() {
        const result = this.getSelectedObject();
        if (this.selectedTreeNode && result && result.length > 0) {
            this.selectedTreeNode.selectedInvok = result.filter(inv => inv && inv.appId === this.selectedTreeNode.id);
        }
        this.afterConfirm.next(this.selectedTreeNode);
        // åæ¶-ç®çæ¯å¨è¿éè®©æ¨¡ææ¡æ¶å¤±
        this.cancle();
    }
    /**
     * åæ¶
     */
    cancle() {
        this.afterCancle.next();
    }

    i18nEntityInitBak() {
        this.i18nService.getI18nJson().subscribe(
            entity => {
                if (entity) {
                    this.title = entity.TitleName;
                    this.i18nEntity = entity;
                    this.i18nService.i18nObj = entity;
                }
            }
        );
    }

    public setCurrentLangCode(langCode: string) {
        this.currentLangCode = langCode;
        this.title = this.getResOnCurrentLangeContext('TitleName');
    }

    public getResOnCurrentLangeContext(resKey: string): string {
        const resValue = i18nAppHelpInfo[this.currentLangCode][resKey];
        return resValue;
    }
}

